import React, { Component } from 'react';
import BackgroundImage from '../images/splash2.jpg';

export default class AboutPage extends Component {

  render() {
    const styleHome = Object.assign({}, styles.AboutPage, {backgroundImage: `url(${this.state.backImgUrl})`});
    return (
      <div className="AboutPage" style={styleHome}>
      </div>
    );
  }

  constructor(props) {
      super(props);
      this.state = { backImgUrl: BackgroundImage };
  }

}

const styles = {
    AboutPage: {
        width: '100%', height: '100%',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover'
    }
}